*{
  margin: 0;
  padding: 0;
   -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #D9C6B0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#container {
 
  width: 100vw;
  height: 80vh;
  position: relative;
  
  
 
}
#metronome{
  
  display: flex;
  flex-direction: column;
  
   justify-content: center;
  align-items: center;
  position: relative;
  height: 500px;


  top:3vh;
}

#metronomeFront{
  position: absolute;
  top:285px;
  width: 320px;

}
#metronomeBack{
  position: absolute;
  top:0px;
  width: 300px;
  z-index: -100;

}

#controls{
  width: 100vw;
  height: 20vh;
  
  position: relative;
  top: 4vh;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  

}










#stick-rect {
  
  width: 7px;
  height: 250px;
  position: absolute;
  top:38px;
  
  background-color: #da0808;
  transform-origin: 3px 250px;
  transform: rotate(-30deg);

  transition-timing-function: cubic-bezier(0, 0.46, 0.69, 0.92);
  
}




#stick {
  width: 7px;
  height: 250px;
  background-color: #222;
}
#rect {
  width: 13px;

  border-top: 24px solid #222;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;

  position: relative;
  bottom: 252px;
  right: 11px;
}
#rectEnd{
  width: 7px;
  height: 10px;
  position: relative;
  bottom: 4px;
  right: -3px;
  background-color: #222;

}

#high,
#low,
#lowfive,
#highfive,#bpm {
  width: 50px;
  height: 50px;
  background-color: #082326;
  border: 1px solid black;
  border-radius: 13px;
  margin: 15px;
 display: flex;
  font-size: 30px;
  color: #d6d5d8;
  text-align: center;
 justify-content: center;
  align-items: center;
}
#bpm{
 width: 100px;
  
 
}



#myRange {
  -webkit-appearance: none;
  width: 40vw;
  height: 18px;
  border-radius: 4px;

  background: #73715F;
  outline: none;
  opacity: 0.7;
  -webkit-transition: .2s;
  transition: opacity .2s;
}

#myRange:hover {
  opacity: 1;
}

#myRange::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 3px;
  background: #082326;
  cursor: pointer;
}

#myRange::-moz-range-thumb {
  width: 25px;
  height: 25px;
  background: #082326;
  cursor: pointer;
}

